const API_KEY = "9a573411b308799ab5eb75456a28f969";
const BASE_PATH = "https://api.themoviedb.org/3";

interface IMovie {
    id: number;
    backdrop_path: string;
    poster_path: string;
    title: string;
    overview: string;
    vote_average: number;
    release_date: string;
}




export interface IGetMoviesResult {
    dates:{
        maximum:string;
        minmum:string;
    };
    page:number;
    results: IMovie[],
    total_pages:number,
    total_results:number
}



export function getMovies() {
    return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then((res) => res.json())
}


